export class Color {
    public hexColorToInt(colour: any) {
        let color = 0;
        const numberRegex = /^[0-9]+$/;
        if (numberRegex.test(colour)) {
            color = colour;
        } else if (colour.length > 7) {
            let colStr = colour.substring(1, 7); // colour.substr(1, 6);
            color = parseInt(colStr, 16);
        } else {
            color = parseInt(colour.slice(1), 16);
        }
        return color;
    }

    public decToHexColor(colour: number) {
        let strColour = colour.toString(16);
        return strColour.padStart(7, '#000000');
    };
}