import axios from 'axios';
import {Markup, MarkupStyle, MarkupType} from '@rib/ige-engine-core';
import {IgeLayer, igeService, IgeViewer, ILayout} from '@rib-4.0/ige-viewer';
import {IAnnotationItem, IAnnotationMarker, ICommentMarkup, IUser, ILayoutName, IMarkupEdit, IDescriptionInfo, IViewSettings, IModelConfig} from "./model";
import {Color, MessageType, UrlConfig} from "./utils";


let _drawingId = '';
let _viewerInstance : IgeViewer;
const _baseServiceUrl: string = location.origin + location.pathname + '../../../services/';

let _currentLayoutId = '';
let _currentAnnotationId = 0;
let _allLayouts: ILayout[] = [];
let _Clerks: IUser[] = [];
let _currentClerkId = 0;

// model config
let _modelPart: any;
let _modelConfig: IModelConfig;
let _lockMarkupUpdateId = '';
let _currentHighlightColor: any;

// annotation
let _selectedAnnotationId = -1;
let _currentModelId = 0;
let _annotationItems: IAnnotationItem[] = [];
let _annoMarkerItems: IAnnotationMarker[] = [];
let _commentMarkups: ICommentMarkup[] = [];
let _isCreateMultipleMarkers = false;
let _currentUpdateMarkupJson = '';

// Show/Hide Model Annotation
let _isAllSelect = false;
let _isShowDefect = false;
let _isShowRfi = false;

// axios instance
const _httpService = axios.create({
    baseURL: _baseServiceUrl,
    timeout: 5000
})

function main(viewer: IgeViewer) {
    console.log('viewer ready');
    _viewerInstance = viewer;

    _viewerInstance.engine.setOnDrawingOpened(onDrawingOpened);
    _viewerInstance.engine.setOnMarkupCreate(onMarkupCreate);
    _viewerInstance.engine.setOnMarkupUpdateRequest(onMarkupUpdateRequest);
    _viewerInstance.engine.setOnMarkupFocus(onMarkupFocus);
    _viewerInstance.engine.setOnMarkupCanEdit(onMarkupCanEdit);
    _viewerInstance.engine.setOnMarkupChange(onMarkupChange);

    console.log('loading drawing -' + _drawingId);
    loadModelConfig().then(() => {
        _viewerInstance.loadDrawing(_drawingId).then(d=> {
            _allLayouts = d.data.layouts;
            for (let item of _allLayouts) {
                let findLayout = _modelConfig.layoutNames.find(e => e.id == item.id);
                if (findLayout) {
                    item.name = findLayout.editName;
                }
            }

            let currentLayoutIndex = _allLayouts.findIndex(e => e.id == _currentLayoutId);
            if (currentLayoutIndex < 0) {
                currentLayoutIndex = 0;
            }

            // @ts-ignore
            console.log(MessageType.onCurrentLayoutPage + currentLayoutIndex+1);
            // Toast.postMessage(MessageType.onCurrentLayoutPage + currentLayoutIndex+1); // Give app the default open page
            const f = _allLayouts[currentLayoutIndex];
            if (f) {
                console.log('opening layout -' + f.id);
                _viewerInstance.openLayout(f.id).then(()=>{
                    _currentLayoutId = f.id;
                    markupLoaded();
                    getCurrentClerk();
                });
            }
       })
    })
}

function onDrawingOpened() {
    console.log('drawing opened:---------');
    markupLoaded();
}

async function loadModelConfig() {
    _httpService.get(_baseServiceUrl + UrlConfig.loadModelConfig + _currentModelId).then(function (res) {
         _modelPart = res.data;
         if (res.data?.Config) {
             _modelConfig = JSON.parse(res.data.Config);
             _currentLayoutId = _modelConfig.layout;
         }
     });
}

function markupLoaded() {
    if (_currentModelId === 0) {
        return;
    }

    loadAnnoMarker(_currentModelId);
}

function loadAnnoMarker(modelId: number) {
    cleanComments();
    _annotationItems = [];
    _annoMarkerItems = [];
    getAnnotations(modelId).then((response) => {
        _annotationItems = response.data ? response.data : response;
        getAnnotationMarkers(modelId).then((response) => {
            if (response && response.data) {
                _annoMarkerItems = response.data;
                for (let item of _annoMarkerItems) {
                    if (item.LayoutId == _currentLayoutId) {
                        let markerItem = _commentMarkups.filter(e => e.AnnoMarkerFk == item.Id && e.AnnotationFk == item.AnnotationFk);
                        let annotationItem = _annotationItems.filter(e => e.Id == item.AnnotationFk);
                        if (markerItem.length === 0 && item.MarkupJson !== null || item.MarkupJson !== '' && annotationItem) {
                            _commentMarkups.push(convertMarkupItem(item, annotationItem[0].ClerkFk, false, null, false));
                        }
                    }
                }

                // isAllSelect = true;
                checkAnnoMarkerShow();
            }
        })
    })
}

function onMarkupCreate(markup: Markup) {
    console.log('Callback OnMarkupCreate: ${markup}');
    saveMarkupInIge(_currentLayoutId, markup);
    return true;
}

function onMarkupChange(markup: Markup) {
    saveMarkupChange(markup);
    return true;
}

function saveMarkupChange(markup: Markup) {
    let markupItem = _commentMarkups.find(e => e.MarkerId == markup.id);
    if (!markupItem) {
        return;
    }
    let markerData = markup.serialize();
    if (markerData === markupItem.Marker) {
        return;
    }
    markupItem.Marker = markerData;

    if (_lockMarkupUpdateId === 'changeMarkerColor') {
        // go on save
    } else if (_lockMarkupUpdateId === null) {
        if (changeSelectMarkerColor(markup)) {
            return;
        }
    }

    if (_lockMarkupUpdateId === 'notSaveMarkup') {
        return;
    }

    if (markupItem.AnnoMarkerFk) {
        markupItem.OriginMarker = markupItem.Marker;
        updateMarkerMarkupJson(markupItem.AnnoMarkerFk, markupItem.AnnotationFk, markup);
        return;
    }
}

// In order to save the original color without highlighting the color
function changeSelectMarkerColor(markUp: Markup) {
    let markupItem = _commentMarkups.find(e => e.MarkerId == markUp.id);
    if (markupItem) {
        markupItem.Marker = markUp.serialize();
        let selectMarker = _commentMarkups.find(e => e.IsSelect);
        let settingHighlightColor = '#0000FF'; // TODO get highlight color
        if (selectMarker && selectMarker.MarkerId === markUp.id && markUp.style.colour.indexOf(settingHighlightColor) > -1 && _lockMarkupUpdateId !== 'notSaveMarkup' && _lockMarkupUpdateId !== 'changeMarkerColor') {
            let type = JSON.parse(JSON.stringify((markUp.style)));
            if (markUp.style.endPointStyle.value === 1) {
                type.endPointStyle = _viewerInstance.linker.MarkupPointStyle.Arrow;
            }
            type.colour = _currentHighlightColor;
            _lockMarkupUpdateId = 'changeMarkerColor';
            _viewerInstance.engine.updateMarkupStyle(markUp.id, type);
            _lockMarkupUpdateId = '';
            highlightColorMarker(selectMarker.MarkerId);
            return true;
        }
    }
    return false;
}

function updateMarkerMarkupJson(id: number, annotationId: number, markup: Markup) {
    let findMarker = _annoMarkerItems.find(e => e.Id == id && e.AnnotationFk == annotationId);
    if (findMarker && markup.serialize) {
        findMarker.MarkupJson = markup.serialize();
        if (findMarker.DescriptionInfo && findMarker.DescriptionInfo.Translated !== markup.text) {
            findMarker.DescriptionInfo.Translated = markup.text;
            findMarker.DescriptionInfo.Modified = true;
        }
        updateMarker(findMarker);
    }
}

function updateMarker(markerItem: IAnnotationMarker) {
    if(_currentUpdateMarkupJson !== markerItem.MarkupJson) {
        _currentUpdateMarkupJson = markerItem.MarkupJson;
        _httpService.post(_baseServiceUrl + UrlConfig.saveAnnoMarker, markerItem)
            .then(res => {
                _currentUpdateMarkupJson = '';
                let markerDto = res.data;
                let findMarker = _annoMarkerItems.find(e => e.Id == markerItem.Id && e.AnnotationFk == markerItem.AnnotationFk);
                // @ts-ignore
                findMarker.Version = markerDto.Version;
            })
    }
}


function onMarkupUpdateRequest(markupId: string, text: string, height: number, bNewMarkup: boolean, markupType: MarkupType, markupStyle: MarkupStyle) {
    console.log('Callback OnMarkupUpdateRequest: ${id}');

    let markerParam = {
        id: markupId,
        color: 0xff0000,
        lineWidth: 1,
        height: height,
        text: text,
        isNewMarkup: bNewMarkup,
        markupStyle: markupStyle
    };

    // edit Markup
    // @ts-ignore
    // Toast.postMessage(MessageType.onMarkupUpdateRequest + JSON.stringify(markerParam));
    return text;
}

function onMarkupFocus(markupId: string) {
    console.log('Callback OnMarkupFocus: ' + markupId);
    for (let item of _commentMarkups) {
        item.IsFocus = item.MarkerId === markupId;
    }
}

// hide the markupCreate & markupEdit & markupDelete option in the context menu when return false
function onMarkupCanEdit(id: string) {
    console.log('OnMarkupCanEdit: ' + id);
    return true;
}

function cleanComments() {
    if (_commentMarkups.length > 0) {
        _commentMarkups.splice(0);
    }
}

function convertMarkupItem(markerItem: IAnnotationMarker, clerkId:number, isShow: boolean, markup: any, isSelect: boolean) {
    let clerkName = '...';
    let user = null;
    let findUser = _Clerks.find(e => e.Id == clerkId);
    if (findUser) {
        user = findUser;
    }

    if (user && user.Id !== 0 && user.Id !== null && user.Id !== undefined) {
        clerkName = user.Name;
    } else {
        _httpService.get(_baseServiceUrl + UrlConfig.getUserByClerk + clerkId).then(function callback(response) {
            user = {Id: clerkId, Name: response.data};
            _Clerks.push(user);
            for (let item of _commentMarkups) {
                if (item.ClerkFk) {
                    let findItem = _Clerks.find(e => e.Id == item.ClerkFk);
                    if (findItem) {
                        item.Clerk = findItem.Name;
                    }
                }
            }
        });
    }

    let markupData = markerItem.MarkupJson;
    let markupId = JSON.parse(markerItem.MarkupJson).id;
    let color = JSON.parse(markerItem.MarkupJson).colour;
    let text = JSON.parse(markerItem.MarkupJson).text;
    if (markup && markup.serialize) {
        markupId = markup.id;
        markupData = markup.serialize();
        color = markup.style.colour;
        text = markup.text;
    }

    let findAnnotationItems = _annotationItems.filter(e => e.Id == markerItem.AnnotationFk);
    let annotationItem = findAnnotationItems[0];
    return{
        Id: markerItem.Id,
        MarkerId: markupId,
        Marker: markupData,
        LayoutId: markerItem.LayoutId,
        ClerkFk: clerkId,
        Clerk: clerkName,
        UpdatedAt: new Date(markerItem.UpdatedAt).toDateString().substr(4),
        IsSelect: isSelect,
        IsDisableGoto: false,
        IsShow: isShow,
        IsFocus: false,
        Color: color,
        Description: text,
        DescriptionInfo: null,
        Content: markerItem.DescriptionInfo != null ? markerItem.DescriptionInfo.Translated : '',
        ModuleName: '',
        AnnotationFk: markerItem.AnnotationFk,
        AnnoMarkerFk: markerItem.Id,
        InfoRequestFk: annotationItem.InfoRequestFk,
        HsqChecklistFk: annotationItem.HsqChecklistFk,
        DefectFk: annotationItem.DefectFk,
        OriginMarker: markupData,
        InsertedAt: markerItem.InsertedAt,
        MarkupJson: markerItem.MarkupJson,
        Version: markerItem.Version
    };
}

function loadIgeMarkup(id: string, markupItem: any) {
    let markups = [];
    markups.push(markupItem);
    let igeMarkupItem = _viewerInstance.engine.getMarkup(id);
    if (!igeMarkupItem) {
        loadIgeMarkups(markups);
    }
}

function saveMarkupInIge(layoutId: string, markUp: Markup) {
    if (markUp.id === '{00000000-0000-0000-0000-000000000000}') {
        return;
    }

    if (_currentModelId > 0) {
        createViewerAnnotation(layoutId, markUp);
        return;
    }
}

function updateAnnoColor(annotationId: number, color: number, hexColor: string, markerId: string, description: string) {
    let annotationItem = _annotationItems.find(e => e.Id == annotationId);
    if (annotationItem) {
        annotationItem.Color = color;
        if (description) {
            if (!annotationItem.DescriptionInfo){
                annotationItem.DescriptionInfo = new class DescriptionInfo implements DescriptionInfo {
                    Translated= '';
                    Description = '';
                    Modified = false
                };
            }
            annotationItem.DescriptionInfo.Translated = description;
            annotationItem.DescriptionInfo.Modified = true;
        }

        updateAnnotation(annotationItem).then(function () {
            for (let markerItem of _commentMarkups) {
                if (markerItem.AnnotationFk === annotationId && markerItem.MarkerId !== markerId) {
                    let markupData = _viewerInstance.engine.getMarkup(markerItem.MarkerId);
                    if (markupData) {
                        let type = markupData.style;
                        if (markupData.style.endPointStyle.value === 1) {
                            type.endPointStyle = _viewerInstance.linker.MarkupPointStyle.Arrow;
                        }

                        type.colour = hexColor;
                        _viewerInstance.engine.updateMarkupStyle(markerItem.MarkerId, type);
                    }
                }
            }
        });
    }
}

function createViewerAnnotation(layoutId: string, markup: Markup) {
    let annotationItem = _annotationItems.find(e => e.Id == _selectedAnnotationId);
    if (_isCreateMultipleMarkers && annotationItem) {
        createMarker(_selectedAnnotationId, layoutId, markup);
        return;
    }
    createMdlAnnotation(layoutId, markup);
}

function createMdlAnnotation(layoutId: string, markup: Markup) {
    newMdlAnnotation().then(function (res) {
        let annotationDto = res;
        if (annotationDto.ClerkFk === 0) {
            annotationDto.ClerkFk = _currentClerkId;
        }

        annotationDto.Color = new Color().hexColorToInt(markup.style.colour);
        updateAnnotation(annotationDto).then(function (response) {
            let annotationDto = response.data ? response.data : response;
            if (annotationDto.Id) {
               // modelAnnotationLoad(annotationDto.Id);
                createMarker(annotationDto.Id, layoutId, markup);
                _selectedAnnotationId = annotationDto.Id;
            }
        });
    });
}

async function newMdlAnnotation() {
    function generateGuid() {
        let guid = '';
        let count = 32;
        while (count--) {
            guid += Math.floor(Math.random() * 16.0).toString(16);
        }
        return guid;
    }

    let dto = {
        Id: 0,
        Uuid: generateGuid(),
        ModelFk: _currentModelId,
        RawType: 0,   // TODO annotationType
        StatusFk: 1,  // TODO Get Default
        PriorityFk: 1,  // TODO Get Default
        CategoryFk: 1,  // TODO Get Default
        EffectiveCategoryFk: 1,  // TODO Get Default
        ClerkFk: _currentClerkId,
        Color: 16711680
    };

    await _httpService.get(_baseServiceUrl + UrlConfig.getNewAnnotationId).then(function (response) {
        dto.Id = (response && response.data) ? response.data : response;

    });
    return dto;
}

async function updateAnnotation(annotationDto: any) {
    let resData: any;
    await _httpService.post(_baseServiceUrl + UrlConfig.saveAnnotation, annotationDto).then(function (res) {
        resData = res.data ? res.data : res;
        let findItem = _annotationItems.find(e => e.Id == resData.Id);
        if (findItem) {
            findItem.DescriptionInfo = resData.DescriptionInfo;
            findItem.Version = resData.Version;
            findItem.RawType = resData.RawType;
            findItem.Color = resData.Color;
        } else {
            _annotationItems.push(resData);
        }
    });

    return resData;
}

function createMarker(annotationId: number, layoutId: string, markup: Markup) {
    let markupData = markup.serialize();

    _httpService.post(_baseServiceUrl + UrlConfig.createAnnoMarker, {PKey1: annotationId}).then(function (res) {
        let markerEntity = res.data ? res.data : res;
        markerEntity.LayoutId = layoutId;
        markerEntity.MarkupJson = markupData;
        _httpService.post(_baseServiceUrl + UrlConfig.saveAnnoMarker, markerEntity).then(function (response) {
            let markerItem = response.data ? response.data : response;
            _annoMarkerItems.push(markerItem);
            let findMarkupItem = _commentMarkups.find(e => e.AnnotationFk == markerItem.AnnotationFk && e.AnnoMarkerFk == markerItem.Id);
            let annotationItem = _annotationItems.find(e => e.Id == markerItem.AnnotationFk);
            if(annotationItem) {
                if (findMarkupItem) {
                    findMarkupItem = convertMarkupItem(markerItem, annotationItem.ClerkFk, true, markup, true);
                } else {
                    _commentMarkups.push(convertMarkupItem(markerItem, annotationItem.ClerkFk, true, markup, true));
                }
            }
        }, function () {

        });
    });
}

function loadIgeMarkups(markups: string[]) {
    // it will be go to 'onMarkupChange' when use 'loadMarkups' function
    _lockMarkupUpdateId = 'notSaveMarkup';
    _viewerInstance.engine.loadMarkups(markups);
    _lockMarkupUpdateId = '';
}

function showMarkupInIge(commentItem: ICommentMarkup) {
    if (commentItem.IsShow) {
        loadIgeMarkup(commentItem.MarkerId, commentItem.Marker);
    } else {
        let markupIds = [];
        markupIds.push(commentItem.MarkerId);
        _viewerInstance.engine.unloadMarkups(markupIds);
    }
}

function checkAnnoMarkerShow() {
    if (_isAllSelect) {
        showAnnoMarkerByAll(true);
    } else {
        if (_isShowDefect) {
            showAnnoMarkerByDefect(true);
        }
        if (_isShowRfi) {
            showAnnoMarkerByInfoRequest(true);
        }
    }
}

function showAnnoMarker(fieldKey: string, isShow: boolean) {
    let showMarkups = [];
    let removeMarkups = [];

    for(let item of _commentMarkups) {
        let hasShow = false;
        if (fieldKey == '') {
            hasShow = true;
        }
        else if (fieldKey == 'DefectFk') {
            if (item.DefectFk != null) {
                hasShow = true;
            }
        }
        else if (fieldKey == 'InfoRequestFk') {
            if (item.InfoRequestFk != null) {
                hasShow = true;
            }
        }
        else if (fieldKey == 'HsqChecklistFk') {
            if (item.HsqChecklistFk != null) {
                hasShow = true;
            }
        }

        if (hasShow) {
            if (isShow) {
                item.IsShow = true;
                let igeMarkupItem = _viewerInstance.engine.getMarkup(item.MarkerId);
                if (!igeMarkupItem) {
                    showMarkups.push(item.OriginMarker ? item.OriginMarker : item.Marker);
                }
            } else {
                item.IsSelect = false;
                item.IsShow = false;
                removeMarkups.push(item.MarkerId);
            }
        }
    }

    if (showMarkups.length > 0) {
        loadIgeMarkups(showMarkups);
    }
    if (removeMarkups.length > 0) {
        _viewerInstance.engine.unloadMarkups(removeMarkups);
    }
}

function selectMarkupById(markupId: string) {
    revertHighlightColor();
    for (let item of _commentMarkups) {
        item.IsSelect = item.MarkerId === markupId;
    }
}

function highlightColorMarker(markerId: string) {
    if (markerId) {
        let selectItem = _commentMarkups.find(e => e.MarkerId == markerId);
        if (selectItem) {
            let markupData = _viewerInstance.engine.getMarkup(selectItem.MarkerId);
            if (markupData) {
                let type = JSON.parse(JSON.stringify(markupData.style));
                if (markupData.style.endPointStyle.value === 1) {
                    type.endPointStyle = _viewerInstance.linker.MarkupPointStyle.Arrow;
                }

                _currentHighlightColor = type.colour;
                selectItem.Color = type.colour;
                type.colour = '#0000FF';//Color.decToHexColor('0x0000FFFF');
                _lockMarkupUpdateId = 'notSaveMarkup';
                _viewerInstance.engine.updateMarkupStyle(markerId, type);
                _lockMarkupUpdateId = '';
            }
        }
    }
}

function revertHighlightColor() {
    let selectItem = _commentMarkups.find(e => e.IsSelect)
    if (selectItem) {
        let removeMarkups = [];
        removeMarkups.push(selectItem.MarkerId);
        _viewerInstance.engine.unloadMarkups(removeMarkups);
        if (selectItem.OriginMarker) {
            let pushMarkups = [];
            pushMarkups.push(selectItem.OriginMarker);
            loadIgeMarkups(pushMarkups);
        }
    }
}

function deleteMarkupOnView(commentItem: ICommentMarkup) {
    let markupIds = [];
    markupIds.push(commentItem.MarkerId);
    _viewerInstance.engine.unloadMarkups(markupIds);
}

function deleteMarkup(commentItem: ICommentMarkup) {
    postMarkupDelete(commentItem);
    deleteMarkupOnView(commentItem);
}

function postMarkupDelete(commentItem: ICommentMarkup) {
    if (commentItem.AnnoMarkerFk) {
        deleteItem(commentItem.AnnoMarkerFk, commentItem.AnnotationFk);
    } else {
        _httpService.get(_baseServiceUrl + UrlConfig.deleteMarkupUrl + commentItem.Id).then(() => {
                console.log('delete [' + commentItem.Id + '] ok')
            })
    }
    _commentMarkups.splice(_commentMarkups.indexOf(commentItem), 1);
}

function deleteItem(id: number, annotationId: number) {
    let deleteSuccess = false;
    let findAnnoMarkerItems = _annoMarkerItems.filter(e => e.AnnotationFk == annotationId);
    if (findAnnoMarkerItems.length === 1) {
        let delAnnotation = _annotationItems.find(e => e.Id == annotationId);
        if (delAnnotation) {
            deleteAnnotation(delAnnotation).then(res => {
                _annotationItems = _annotationItems.filter(e => e.Id !== annotationId);
            })

        }
    } else {
        let delAnnoMarker =  _annoMarkerItems.find(e => e.Id == id && e.AnnotationFk == annotationId);
        if (delAnnoMarker) {
            deleteAnnoMarker(delAnnoMarker).then(res => {
                _annoMarkerItems.filter(e => !(e.Id === id && e.AnnotationFk === annotationId));
            })
        }
    }
}

function getCurrentClerk() {
    if (_currentClerkId === 0) {
        _httpService.get(_baseServiceUrl + UrlConfig.getClerkByClient).then(function callback(response) {
            _currentClerkId = response.data.Id;
        });
    }
}

function saveCurrentLayout(layoutId: string) {
    _modelConfig.layout = layoutId;
    _modelPart.Config = JSON.stringify(_modelConfig);
    _httpService.post(_baseServiceUrl + UrlConfig.saveModelConfig, _modelPart).then(res => {
        _modelPart = res.data;
    })
}

function getAnnotations(modelId: number): Promise<any>  {
    return _httpService.get(_baseServiceUrl + UrlConfig.getAnnotations + modelId);
}

function getAnnotationMarkers(modelId: number): Promise<any>  {
    return _httpService.get(_baseServiceUrl + UrlConfig.getAnnotationMarkers + modelId);
}

function deleteAnnotation(annotationDto: IAnnotationItem) {
    return _httpService.post(_baseServiceUrl + UrlConfig.deleteAnnotation, annotationDto);
}

function deleteAnnoMarker(annoMarkerDto: IAnnotationMarker) {
    return _httpService.post(_baseServiceUrl + UrlConfig.deleteAnnoMarker, annoMarkerDto);
}

function getFontSize() {
    let num: any = _viewerInstance.engine.optionAsFloat('MARKUP_FONT_SCALE');
    if (num.toFixed) {
        num = num.toFixed(2);
    }

    return Number ? Number(num) : num;
}

function updateAnnotationDescription(markerId: string, description: string) {
    let findMarkerItem = _commentMarkups.find(e => e.MarkerId == markerId);
    let annoId = (findMarkerItem && findMarkerItem.AnnotationFk) ? findMarkerItem.AnnotationFk : _currentAnnotationId;
    let findItem = _annotationItems.find(e => e.Id == annoId);
    if (!findItem) {
        return;
    }
    if (!findItem.DescriptionInfo)
    { findItem.DescriptionInfo = new class DescriptionInfo implements IDescriptionInfo {Description = ''; Translated = ''; Modified = false};}
    findItem.DescriptionInfo.Translated = description;
    findItem.DescriptionInfo.Modified = true;


    let annotationItem = _annotationItems.find(e => e.Id == annoId);
    if (!annotationItem) {
        annotationItem = findItem;
    }
    if (isNaN(annotationItem.EffectiveCategoryFk)) {
        annotationItem.EffectiveCategoryFk = findItem.EffectiveCategoryFk;
    }
    if (!annotationItem.DescriptionInfo){
        annotationItem.DescriptionInfo = new class DescriptionInfo implements IDescriptionInfo {Description = ''; Translated = ''; Modified = false};
    }
    annotationItem.DescriptionInfo.Translated = description;
    annotationItem.DescriptionInfo.Modified = true;
    updateAnnotation(annotationItem).then();
}

function updateAnnoMarkerDescription(id: string, comment: string) {
    let findMarkupItem = _commentMarkups.find(e => e.MarkerId == id);
    if (findMarkupItem) {
        // @ts-ignore
        let findMarker = _annoMarkerItems.find(e => e.Id == findMarkupItem.Id && e.AnnotationFk == findMarkupItem.AnnotationFk);
        findMarkupItem.Description = comment;
        if (findMarker) {
            findMarker.DescriptionInfo.Translated = comment;
            findMarker.DescriptionInfo.Modified = true;
        }
    }
}

function initHttpService(token: string, clientContext: string) {
    _httpService.defaults.headers.post['Content-Type'] = 'application/json';
    _httpService.interceptors.request.use(
        config => {
            config.headers['Authorization'] = 'Bearer '+ token
            config.headers['Client-Context'] = clientContext;
            return config;
        },
        error => {
            return Promise.reject(error);
        });

    _httpService.interceptors.response.use(function (response) {
        return response
    }, async function(error) {
        console.dir(error);
        if (error.response && error.response.status === 401) {
            // @ts-ignore
            console.log(MessageType.onTokenFailed);
            //Toast.postMessage(MessageType.onTokenFailed);
        }else
        {
            // @ts-ignore
            console.log(MessageType.onHttpError + error.message);
            //Toast.postMessage(MessageType.onHttpError, error.message);
        }
    });
}



// #region For App call function
function Start(token: string, clientContext: string, modelId: number) {
    _currentModelId = modelId;
    initHttpService(token, clientContext);

    _httpService.get(_baseServiceUrl + UrlConfig.getModel + modelId).then((response) => {
        _drawingId = response && response.data?.Uuid;
        if(_drawingId && _drawingId !== '') {
            igeService.configure({
                useWorker: window.crossOriginIsolated,
                keyboardElement: '#canvas',
                serverBaseUrl: _baseServiceUrl + 'model/igeviewer',
                clientBaseUrl: window.crossOriginIsolated ? 'ige/' : 'ige/nop/'
            });

            console.log('start engine');
            igeService.startEngine(document.querySelector('#canvas') as HTMLCanvasElement).then(main);
        }else {
            // @ts-ignore
            console.log(MessageType.onUuidNotFound);
            // Toast.postMessage(MessageType.onUuidNotFound);
        }
    })
}

function gotoLayout(layoutId: string) {
    _viewerInstance.openLayout(layoutId).then(() => {
        _currentLayoutId = layoutId;
        saveCurrentLayout(layoutId);
        markupLoaded();
    });
}

function getAllLayouts() {
    return JSON.stringify(_allLayouts);
}

function getAllLayers() {
    let layers = new IgeLayer(_viewerInstance).layers;
    let allLayersArr = [];

    let length: number = layers.length;
    for(let i=0; i<length; i++) {
        let tempLayer = {
            name: layers[i].name,
            index: layers[i].index,
            isOn: layers[i].isOn
        }
        allLayersArr.push(tempLayer);
    }

    return JSON.stringify(allLayersArr);
}

function zoomIn() {
    _viewerInstance.engine.zoomIn();
}

function zoomOut() {
    _viewerInstance.engine.zoomOut();
}

function rotateLeft() {
    _viewerInstance.engine.rotateDrawing(-90);
}

function rotateRight() {
    _viewerInstance.engine.rotateDrawing(90);
}

function fix2Viewport() {
    _viewerInstance.engine.resetView();
    for(let item of _commentMarkups) {
        item.IsSelect = false;
        item.IsShow = false;
        deleteMarkupOnView(item);
    }
    _isAllSelect = false;
}

function showAllLayers() {
    _viewerInstance.engine.showAllLayers();
}

function invertFilter(){
    _viewerInstance.engine.invertLayers();
}

function setLayerVisibility(layerIndex: number, visible: boolean) {
    let viewport = ''; // TODO: Support viewport layers.
    _viewerInstance.engine.updateLayer(viewport, layerIndex, visible, true);
}

function createMarkup(markupType: string, isCreateMultipleMarkers: boolean, annotationId: number, isCloudStyle: boolean, isEndPointStyle: boolean) {
    _isCreateMultipleMarkers = isCreateMultipleMarkers;
    _selectedAnnotationId = annotationId;

    let defaultMarkupType = {colour: '#FF0000', shape: 0, startPointStyle: _viewerInstance.linker.MarkupPointStyle.None, endPointStyle: _viewerInstance.linker.MarkupPointStyle.Arrow, lineOptions: 0, linePatternId: 0, lineThickness: 0};
    defaultMarkupType.lineOptions = isCloudStyle ? _viewerInstance.linker.MarkupLineOptions.Cloud.value : _viewerInstance.linker.MarkupLineOptions.None.value;
    switch (markupType) {
        case 'Point':
            defaultMarkupType.lineOptions = _viewerInstance.linker.MarkupLineOptions.None.value;
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Point, defaultMarkupType);
            break;
        case 'Line':
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Line, defaultMarkupType);
            break;
        case 'Arrow':
            defaultMarkupType.endPointStyle = _viewerInstance.linker.MarkupPointStyle.Arrow;
            defaultMarkupType.lineOptions = _viewerInstance.linker.MarkupLineOptions.None.value;
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Line, defaultMarkupType);
            break;
        case 'Rectangle':
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Region, defaultMarkupType);
            break;
        case 'Ellipse':
            defaultMarkupType.shape = _viewerInstance.linker.MarkupRegionShape.Ellipse.value;
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Region, defaultMarkupType);
            break;
        case 'Freehand':
            defaultMarkupType.endPointStyle = isEndPointStyle ? _viewerInstance.linker.MarkupPointStyle.Arrow : _viewerInstance.linker.MarkupPointStyle.None;
            _viewerInstance.engine.createMarkup(_viewerInstance.linker.MarkupType.Freehand, defaultMarkupType);
            break;
    }
}

function getCommentMarkups() {
    return JSON.stringify(_commentMarkups);
}

function showAnnoMarkerByAll(isShow: boolean) {
    _isAllSelect = isShow;
    _isShowRfi = isShow;
    _isShowDefect = isShow;
    return showAnnoMarker('', isShow);
}

function showAnnoMarkerByDefect(isShow: boolean) {
    _isShowDefect = isShow;
    return showAnnoMarker('DefectFk', isShow);
}

function showAnnoMarkerByInfoRequest(isShow: boolean) {
    _isShowRfi = isShow;
    return showAnnoMarker('InfoRequestFk', isShow);
}

function selectMarkup(commentItemId: number) {
    let commentItem = _commentMarkups.find(e => e.Id == commentItemId);
    if (!commentItem) {
        return false;
    }

    let currentSelectState = commentItem.IsSelect;
    selectMarkupById(commentItem.MarkerId);

    if (!currentSelectState) {
        commentItem.IsSelect = true;
        if (!commentItem.IsShow) {
            commentItem.IsShow = true;
        }

        if (commentItem.IsShow) {
            loadIgeMarkup(commentItem.MarkerId, commentItem.Marker);
        } else {
            deleteMarkupOnView(commentItem);
        }

        let isSelectByComment = true;
        highlightColorMarker(commentItem.MarkerId);
    }
}

function fontSizeChange(isIncrease: boolean) {
    let fontSizeNum = 1;
    if (isIncrease) {
        fontSizeNum = getFontSize() * 1.2;
    }else {
        fontSizeNum = getFontSize() * 0.8;
    }

    console.log("FontSize set to: " + fontSizeNum);
    _viewerInstance.engine.setOptionAsFloat('MARKUP_FONT_SCALE', fontSizeNum);
}

function getMarkupEditInfo(markupId: string) {
    let markup = _viewerInstance.engine.getMarkup(markupId);
    let commentMarkup = _commentMarkups.find(e => e.MarkerId == markupId)!;
    if (commentMarkup) {
        let annotationItem = _annotationItems.find(e => e.Id == commentMarkup.AnnotationFk);
        let markupStyle = markup.style;
        let editInfo = {
            MarkupId: markupId,
            AnnoColour: markupStyle.colour,
            AnnoDescription: annotationItem?.DescriptionInfo?.Description,
            Description: markup.text,
            FontHeight: markup.fontHeight,
            LineWidth: markupStyle.lineThickness,
            IsReadOnlyColor: false
        }

        return JSON.stringify(editInfo);
    }

    return '';
}

function updateMarkupEditInfo(updateInfo: string) {
    let result: IMarkupEdit = JSON.parse(updateInfo);
    if (result){
        let isAnnoSave = false;
        let originalMarkup = _viewerInstance.engine.getMarkup(result.MarkupId);
        if ((result.AnnoColour !== originalMarkup.style.colour || result.IsReadOnlyColor || result.LineWidth !== originalMarkup.style.lineThickness) &&
            (result.Description !== originalMarkup.text || result.FontHeight !== originalMarkup.fontHeight)) {
            _lockMarkupUpdateId = 'notSaveMarkup';
        }

        if (result.AnnoColour !== originalMarkup.style.colour || result.IsReadOnlyColor || result.LineWidth !== originalMarkup.style.lineThickness) {

            let hexColor = result.AnnoColour;
            let markupData = _viewerInstance.engine.getMarkup(result.MarkupId);
            let type = JSON.parse(JSON.stringify(markupData.style));
            if (markupData.style.endPointStyle.value === 1) {
                type.endPointStyle = _viewerInstance.linker.MarkupPointStyle.Arrow;
            }
            type.colour = hexColor;
            type.lineThickness = result.LineWidth;
            let findMarkerItem = _commentMarkups.find(e => e.MarkerId == result.MarkupId);
            if (findMarkerItem && findMarkerItem.AnnotationFk) {
                isAnnoSave = true;
                updateAnnoColor(findMarkerItem.AnnotationFk, 1, hexColor, result.MarkupId, result.AnnoDescription);
            }
            _viewerInstance.engine.updateMarkupStyle(result.MarkupId, type);
        }
        _lockMarkupUpdateId = '';
        if (result.Description !== originalMarkup.text || result.FontHeight !== originalMarkup.fontHeight) {
            updateAnnoMarkerDescription(result.MarkupId, result.Description);
            _viewerInstance.engine.updateMarkupText(result.MarkupId, result.Description, result.FontHeight);
        }

        let commentMarkup = _commentMarkups.find(e => e.MarkerId == result.MarkupId)!;
        if (commentMarkup) {
            let annotationItem = _annotationItems.find(e => e.Id == commentMarkup.AnnotationFk);
            if (result.AnnoDescription !== annotationItem?.DescriptionInfo?.Description && !isAnnoSave) {
                updateAnnotationDescription(result.MarkupId, result.AnnoDescription);
            }
        }
    }
}

function deleteMarkupById(markupId: string) {
    let markup = _commentMarkups.find(e => e.MarkerId == markupId);
    if (markup) {
        deleteMarkup(markup);
    }
}

function deleteAllMarker() {
    for (let item of _commentMarkups) {
        deleteMarkup(item);
    }
}

function refreshToken(token: string, clientContext: string) {
    _httpService.interceptors.request.use(
        config => {
            config.headers['Authorization'] = 'Bearer '+ token
            config.headers['Client-Context'] = clientContext;
            return config;
        },
        error => {
            return Promise.reject(error);
        });
}

function applyViewSettings(settings: string) {
    let viewerSettings: IViewSettings = JSON.parse(settings);
    if (viewerSettings) {
        _viewerInstance.engine.setDrawingDisplayEnabled(_viewerInstance.linker.DrawingDisplaySetting.WhiteBackground, viewerSettings.white);
        _viewerInstance.engine.setDrawingDisplayEnabled(_viewerInstance.linker.DrawingDisplaySetting.UseMonochrome, viewerSettings.monochrome);
        _viewerInstance.engine.setDrawingDisplayEnabled(_viewerInstance.linker.DrawingDisplaySetting.ShowText, viewerSettings.text);
        _viewerInstance.engine.setDrawingDisplayEnabled(_viewerInstance.linker.DrawingDisplaySetting.UseLineStyle, viewerSettings.lineStyle);
        _viewerInstance.engine.setDrawingDisplayEnabled(_viewerInstance.linker.DrawingDisplaySetting.UseLineWeight, viewerSettings.lineWeight);

        _currentHighlightColor = viewerSettings.highlightColor;
        // TODO save config to database
    }
}

function saveLayoutNames(layoutNames: string) {
    let newLayoutNames: ILayoutName[] = JSON.parse(layoutNames);
    for (let item of newLayoutNames) {
        let findItem = _modelConfig.layoutNames.find(e => e.id == item.id);
        if (findItem) {
            findItem.editName = item.editName;
        }
    }

    _modelPart.Config = JSON.stringify(_modelConfig);
    _httpService.post(_baseServiceUrl + UrlConfig.saveModelConfig, _modelPart).then(res => {
            _modelPart = res.data;
        })
}

